.Item-container{
    display: -ms-flexbox;
    display: flex;
    
  
    height: 250px;
    width:20%;
    min-width: 300px;
    max-width: 500px;
    margin-top: 10px;
    margin-right: 10px;
    margin-bottom: 10px;
  

}
.Item-name{
    font-family: iCiel-Altus;
    color:white;
    font-size: 50px;
    height: 60px;
}
.Item-price{
    font-family:iCielNovecentosans-Light ;
    color:white;
    font-size: 25px;
}
.button_add_to_cart{
  
    position: absolute;
    top:0;
    right: 0;
    padding: 0;
    width: 50px;
    height: 50px;
}
.button_add_to_cart button{

        width: 100%;
        height: 100%;
      padding: 0;
      visibility: hidden;
  }
  
  .button_add_to_cart button img{
    
        width: 100%;
        height: 100%;
      padding: 0;
  }

  .button_quantity{
    display: -ms-flexbox;
    display: flex;
  

    
    width: 40px;
    height: 40px; 
    -ms-flex-align: center; 
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-line-pack: center;
        align-content: center;
    border-radius: 50%;
    opacity: 0.7;
    
  }
  .button_quantity:hover{
  
    opacity: 1;

  }
  .button_quantity:active{
    
      width: 41px;
      height: 41px;
    
    }
  
  .button_quantity img{
   
    width:100%;
    height: 100%; 
   
  }

.quantity{
    display: -ms-flexbox;
    display: flex;
    width: 25%;
    height: 40px;
    border-radius:20px;
    background: white;
    
    -ms-flex-pack: center;
    
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    font-family: iCielNovecentosans-Medium;
    font-size: 30px;
    margin-left: 10px;
    margin-right: 10px;
}


.Background{
 
    position: relative;

    display: -ms-flexbox;

    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-item-align: center;
        align-self: center;
    
    height: 100%;
    width: 100%;

}

@media only screen and (max-width: 450px){
    .Item-container{
        float: none;
        min-width: 100px;
        width: 80%;
    }
}


